import './Block.css';
import Lottie from 'react-lottie-player'

interface ContainerProps {
  bgColor: string;
  txColor: string;
  img?:any;
  text1?:any;
  text2?:any;
  imgWidth?:number;
  lottie?: any;
  heroText?:string;
  link?:string;
 }

export default function Block(props:ContainerProps){

  function TextLines(textList:any){
    let index = 0;
    return textList.map((text:string)=>{
      index++;
      return (
        <div key={"text-"+index} className='block-textLine'>
          {text}
        </div>
      )
    })
  }


  return (
    <div className="block-container" style={{backgroundColor:props.bgColor, color:props.txColor}}>
      <div className='block-text'> 
      {props.heroText?(
      <h1>    
        {props.heroText}
      </h1>):null}
      {props.text1 ? TextLines(props.text1) : null} 
      </div>
      <div className='block-img' > 
      {props.lottie ? (
          <Lottie
          loop
          animationData={props.lottie}
          play
          style={{ width: props.imgWidth,    margin: "0 auto"}}
        />
      ) : (

          <img onClick={()=>{
            if(props.link){
              window.open(props.link)
            }
            
          }} 
          
          src={props.img} style={{width:props.imgWidth, cursor: props.link ? "pointer":"cursor"}}/>
      )
      }
      </div>
      <div className='block-text'> 
        {props.text2 ? TextLines(props.text2) :null}
      </div>
    </div>
  );
};
