import { IonButton, IonCol, IonLabel, IonRange, IonRow } from "@ionic/react";
import { useState } from "react";

interface ContainerProps {}

export default function Pricing(props: ContainerProps) {
  const [capita, _capita] = useState<any>(100000);
  const [price, _price] = useState<any>(1.25);

  function currencyFormat(num:number) {
    return '$' + num.toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
 }

  return (
    <div
      className="cta-container"
      style={{
        backgroundColor: "#eee",
        width: "100%",
        textAlign: "center",
        paddingTop: 100,
        paddingBottom: 100,
      }}
    >
      <h1>Pricing</h1>
      <IonRow>
        <IonCol offsetMd="3" sizeMd="6">
          <IonRange
            min={10000}
            max={300000}
            step={2000}
            value={capita}
            onIonChange={(e) => {
              _capita(e.detail.value);
              let cap:any = e.detail.value;
              let per = 1
              let pr = ((cap * per + 50000) / cap)/1.2;
              _price(pr)
              console.log(cap);
            }}
            color="secondary"
          >
            <IonLabel style={{ fontSize: 40, fontWeight: 900 }} slot="start">
              10k
            </IonLabel>
            <IonLabel style={{ fontSize: 40, fontWeight: 900 }} slot="end">
              300k
            </IonLabel>
          </IonRange>
          <h2><b>{capita/1000}k</b></h2>
          Service Area <br />

          
           
        </IonCol>
      </IonRow>
      <IonRow>
      <IonCol offsetMd="3" sizeMd="2">
          <h1>$2,000</h1>
          One Time Setup
          </IonCol>    
          <IonCol  sizeMd="2">
              {capita == 300000 ? (
                  <h1>
                     <b>CALL</b> 
                  </h1>
              ):(
                  <h1><b>{currencyFormat(price / 12)}</b></h1>
              )}
              <b>Per Capita <br/>Per Year</b><br/>
              <span style={{fontSize:12}}>Billed Yearly</span> 
          </IonCol>
          <IonCol  sizeMd="2">
              {capita == 300000  ? (
                  <h1>
                     CALL
                  </h1>
              ):(
                  <h1>{currencyFormat(price * 1.33 / 12)}</h1>
              )}
          
          <b>Per Capita <br/>Per Year</b><br/>
             <span style={{fontSize:12}}>Billed Monthly</span> 
          </IonCol>
    
      </IonRow>
    </div>
  );
}
